.elle-checkbox {
  display: none !important;
}
.elle-checkbox:not(:checked) + .elle-target {
  display: none !important;
}

.elle-switcher .elle-radio {
  display: none;
}
.elle-switcher .elle-radio[data-nth="1"]:not(:checked) ~ .elle-target[data-nth="1"]:not(.animated) {
  display: none !important;
}
.elle-switcher .elle-radio[data-nth="1"]:not(:checked) ~ .elle-target[data-nth="1"]:not(.animated) {
  position: absolute !important;
  visibility: hidden !important;
  z-index: -1 !important;
  pointer-events: none !important;
  user-select: none !important;
}
