.elle-target[class*=elle-animated] {
  animation: top 0.4s ease;
  animation-delay: 0s;
  animation-fill-mode: forwards;
}
.elle-target[class*=elle-animated].elle-animated-left {
  animation-name: left;
}
.elle-target[class*=elle-animated].elle-animated-right {
  animation-name: right;
}
.elle-target[class*=elle-animated].elle-animated-bottom {
  animation-name: bottom;
}

@keyframes top {
  from {
    position: relative;
    top: -20px;
    opacity: 0;
  }
  to {
    position: relative;
    top: 0px;
    opacity: 1;
  }
}
@keyframes left {
  from {
    position: relative;
    left: -20px;
    opacity: 0;
  }
  to {
    position: relative;
    left: 0px;
    opacity: 1;
  }
}
@keyframes right {
  from {
    position: relative;
    right: -20px;
    opacity: 0;
  }
  to {
    position: relative;
    right: 0px;
    opacity: 1;
  }
}
@keyframes bottom {
  from {
    position: relative;
    bottom: -20px;
    opacity: 0;
  }
  to {
    position: relative;
    bottom: 0px;
    opacity: 1;
  }
}
